<template>
  <div class="detail" v-loading="loading">
    <img v-if="item.albumPics" :src="item.albumPics"/>
    <div class="title">{{item.name}}</div>
    <div class="info">
      <div class="cell">
        <div class="headline">售价</div>
        <div class="number">￥{{item.price}}</div>
      </div>
      <div class="cell">
        <div class="headline">活动佣金</div>
        <div class="number red">{{item.rebateRate}}%</div>
      </div>
      <div class="cell">
        <div class="headline">最多可赚</div>
        <div class="number red">￥{{item.price * (item.rebateRate * 0.01)}}</div>
      </div>
    </div>
    <div class="copy">复制链接</div>
    <div class="card-title">·····················   直播手卡   ·····················</div>
    <img :src="item.albumPics"/>
    <div class="card-title">·····················   商品详情   ·····················</div>
    <div class="content" v-html="item.detailHtml"></div>
  </div>
</template>

<script>
import { fetchProductDetail } from '@/api/tkpro'
export default {
  name: 'detail',
  data() {
    return {
      id: 0,
      item: null,
      loading: false,
    }
  },
  created(){
    this.id = this.$route.query.id
    this.loadData()
  },
  beforeRouteUpdate (to, from, next) {
    this.id = to.query.id
    this.loadData()
  },
  methods:{
    loadData () {
      this.loading = true
      fetchProductDetail(this.id).then(res => {
        if (res.code !== 200) return this.$message.error(res.message);
        this.item = res.data
        this.loading = false
      }).catch(error => {
        console.log(error);
        this.loading = false
      })
    }
  }
}
</script>

<style rel="stylesheet/scss" lang="scss" scoped>
.detail {
  background: #ffffff;
  padding: 20px 0;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  align-items: center;
  img {
    width: 400px;
    height: 400px;
    margin-bottom: 8px;
  }
  .title {
    color: #101010;
    font-size: 28px;
    margin-bottom: 20px;
  }
  .info {
    width: 490px;
    height: 109px;
    line-height: 20px;
    border-radius: 10px 10px 10px 10px;
    border: 1px solid #BBBBBB;
    display: flex;
    flex-direction: row;
    padding: 28px 65px;
    justify-content: space-between;
    margin-bottom: 20px;
    .cell {
      display: flex;
      flex-direction: column;
      .headline {
        margin-bottom: 8px;
      }
      .number {
        text-align: center;
      }
      .red {
        color: #FF1111;
      }
    }
  }
  .copy {
    width: 128px;
    height: 44px;
    border-radius: 4px;
    background-color: #A4ADB3;
    color: #ffffff;
    font-size: 14px;
    text-align: center;
    padding: 14px;
    margin-bottom: 20px;
    cursor: pointer;
  }
  .card-title {
    color: #101010;
    font-size: 28px;
    margin-bottom: 20px;
  }
  .content {
  }
}
</style>



